/* harmony default export */ __webpack_exports__["a"] = ([{temaId:'mision',title:'Misión',texto:'Asesorar y brindar a empresas y particulares las herramientas, tanto en el al ámbito ambiental como sanitario, promoviendo y facilitando el desarrollo económico sostenible, tomando como marco de referencia: La normatividad vigente, los más altos estándares de calidad y la responsabilidad social.',bullet:''},{temaId:'vision',title:'Visión',texto:'Convertirnos en una de las empresas consultoras más grandes de Colombia, con un equipo de trabajo profesional, orientado a la mejora continua, por medio del cual, empresas y particulares adquieren la capacidad de generar un desarrollo económico sostenible y socialmente responsable, aportándole lo mejor a nuestro país.',bullet:''},{temaId:'que_hacemos',title:'¿Qué hacemos?',texto:'En IAMSA Solutions transformamos su inversión en ahorro y resultados. Brindamos consultoría y acompañamiento personalizado de calidad en temas ambientales, de sostenibilidad e innovación ofreciendo de manera gratuita el diagnóstico de la necesidad de su empresa. Llevamos la innovación y el desarrollo sostenible a su compañía haciendo visibles iniciativas que como la suya están cambiando el mundo.',bullet:''},{temaId:'quienes_somos',title:'¿Quiénes somos?',texto:'Somos una empresa comprometida con el desarrollo sostenible, conformada por un equipo multidisciplinar de calidad en áreas de ingeniería y gerencia de innovación, cuyo objetivo es brindar las mejores alternativas en problemáticas ambientales y de sostenibilidad a nuestros clientes.',bullet:''},{temaId:'como_lo_hacemos',title:'¿Cómo lo hacemos?',texto:'“Con IAMSA Solutions no solo podrá gozar de los mejores servicios con alta calidad a bajo costo, sino de atención inmediata, eficiente, profesional y personalizada; porque para IAMSA nuestros clientes son muy importantes, y no dudamos en considerar el éxito de ellos, como nuestro.  En IAMSA construimos a través de nuestro trabajo, un mundo mejor.”',bullet:'A través de: _ Servicios con alta calidad a bajo costo: Usted contará con atención inmediata, eficiente, profesional y personalizada _ Trazabilidad: Nuestro cliente conocerá en tiempo real el estado de su trámite o proyecto a través de nuestra plataforma virtual . _ Metodologías innovadoras: Co-creamos soluciones adaptadas a su necesidad y presupuesto'}]);

